export class MessageForPlayer {
    constructor(username, message) {
        this.username = username;
        this.message = message;
    }
}
export const noCleavagePleaseWait = (username) => ({
    username: username,
    message: `Il n'y a pas de clivage en cours ${username}. Attends un peu.`
});
export const welcomePlayerMessage = (username) => ({
    username: username,
    message: welcomeMessage(username)
});
const welcomeMessage = (username) => `Bienvenue ${username} au jeu du clivage! Tu peux tapper '!h' pour l'aide.`;
